/* App.css */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
/* Pour Firefox */
html {
  background-color: black;
  scrollbar-width: none; /* Firefox 64 */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.content {
  overflow-x: hidden !important;
  /* height: 100vh; */
  scrollbar-width: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/* Pour Chrome, Safari et Edge */
html::-webkit-scrollbar {
  display: none;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
}
header {
  /* position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center; */
  color: white;
  background-color: rgb(24, 25, 27);
  width: 100%;
  margin: 0;
  position: sticky;
  top: 0;
  display: flex;
  text-align: center;
  justify-content: center;
  margin-bottom: 20px;
  z-index: 13;
  line-height: 1.2;
  height: 35px;
}
hr {
  margin: 10px 30px;
}
.header-title {
  text-align: center;
  color: white;
  width: 100%;
  height: 35px;
  line-height: 2;
}
.header-options {
  width: 300px;
  display: flex;
}
.options-menu {
  display: flex;
  width: 100%;
  height: 35px;
}
.options-item {
  color: white;
  background-color: rgb(24, 25, 27);
  width: 75px;
  font-size: 1.6em;
  text-align: center;
  color: white;
}
.options-item:hover {
  background-color: rgb(208, 208, 208);
  cursor: pointer;
  color: black;
}
.selected-option {
  background-color: rgb(145, 145, 145) !important;
  color: black !important;
}
.options-window {
  width: 300px;
  display: block;
  position: relative;
  top: 35px;
  left: -300px;
  /* height: 300px; */
}
.options-window-sub {
  width:300px;
  color: black;
  background-color: white;
}
.sort-content label {
  margin-left:5px;
}
.sort-scene {
  display: flex;
  font-size: 14px;
  padding: 15px;
}
.sort-scene-column {
  display: block;
  width: 100%;
  text-align: left;
}
