}
.scene-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 1%;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  padding: 0;
  border-radius: 5px;
  /* flex: 1; */
  /* max-width: 200px; */
  padding-bottom: 5px;
  min-width: 0;
  margin-bottom: 20px;
  max-width: 300px;
}
.swiping-scene-left {
  transition: transform 0.2s ease-out;
  transform: translateX(-50vw);
}
.swiping-scene-right {
  transition: transform 0.2s ease-out;
  transform: translateX(+50vw);
}
