.scene-bands {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  background-image: linear-gradient( to bottom, rgba(255,255,255,0.1), rgba(255,255,255,0.1) 50%, transparent 50%, transparent);
  /* opacity: 50%; */
  /* background-size: 13% 13%; */
  background-size: 13% 120px;
  /* background-position: 3.571% 3.571%; */
  height: 960px;
  /* overflow: hidden; */
}
.band-container {
  padding:0;
  margin: 0;
  display: flex;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  width: 92%;
  border: 1px solid #000000;
  border-radius: 5px;;
  font-size: 0.5em;
  overflow: hidden;
  align-items: center;
}
.band-tag h4{
  font-size: 1vw;
  font-size: clamp(1em, 1vw, 1.5em);
}
.band-container:hover {
  cursor: pointer;
  border-color: white;
}
.selected-group {
  box-shadow: 0px 0px 0px 5px rgb(255, 255, 255) inset !important;
}
.tagged {
  background-color: #a61f1f;
}
.hours {
  position: absolute;
  font-size: 0.1em;
  left: 40%;
  color: white;
  opacity: 0.7
}
.group-details {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  animation: slide-in 0.5s forwards;
  z-index: 10;
  background-color: white;
  width: 300px;
  padding-top: 50px;
  overflow: scroll;
}
.appreciation {
  margin: 20px 0 0 10px;
  padding-top: 20px;
  text-align: center;
  border-top: 1px solid black;
}
.appreciation textarea {
  margin-top: 15px;
  width: 100%;
  height: 80px;
  font-size : 0.8em;
  border-radius: 10px;
  resize: none;
}
.group-details.closing {
  animation: slide-out 0.5s forwards;
}
@keyframes slide-in {
  from { transform: translateX(-100%); }
  to { transform: translateX(0); }
}
@keyframes slide-out {
  from { transform: translateX(0); }
  to { transform: translateX(-100%); }
}

.group-name{
  color: white;
  text-align: center;
  margin: 10px;
  display: flex;
  justify-content: center;
}
.group-name h3 {
  padding: 3px 10px;
  border-radius: 20px;
  font-size: 1.8em;
}
.group-style{
  margin-top: 10px;
  margin-left: 10px;
}
.group-style span {
  padding: 3px 10px;
  border-radius: 20px;
  font-size: 0.8em;
  font-weight: bold;
}
.group-infos{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px;
  font-size: 0.8em;
}
.group-hours{
  font-size: 0.8em;
}
.group-bio{
  margin: 10px;
  text-align: justify;
  font-size: 0.9em;
}
.group-ffo{
  margin: 10px;
  text-align: justify;
  font-size: 0.8em;
  background-color: lightgray;
  padding: 3px;
  border-radius: 3px;
  font-style: italic;
}
.group-participation{
  margin: 10px;
  text-align: right;
  font-size: 0.6em;
  /* background-color: lightgray; */
  padding: 3px;
  border-radius: 3px;
  font-style: italic;
}
.group-playlists {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px;
}
.group-playlists span {
  transition: transform 0.2s ease-in-out;
}
.group-playlists span:hover {
  transform: scale(1.2);
  cursor: pointer;
}
.close-button{
  cursor: pointer;
  font-size: 10px;
  position: absolute;
  top: 45px;
  right: 0;
  border-radius: 10px;
  height: 20px;
  width: 20px;
  text-align: center;
}

@media screen and (max-width: 600px) {
  .sort-compact {
    display: none;
  }
  .group-details {
    position: fixed;
    left: 0;
    top: calc(100% - 200px);
    height: 200px;
    animation: mobile-slide-in 0.5s forwards;
    z-index: 10;
    background-color: white;
    width: 100%;
    padding-top: 0;
    overflow: scroll;
    padding-right:10px;
  }

  .group-name h3 {
    font-size: 1em;
  }
  .group-style span {
    font-size: 0.6em;
  }
  .group-infos{
    font-size: 0.6em;
  }
  .group-hours{
    font-size: 0.6em;
  }
  .group-bio{
    font-size: 0.5em;
  }
  .group-ffo{
    font-size: 0.5em;
  }
  .close-button{
    top: 10px;
    right: 0;
  }
  .group-details.closing {
    animation: mobile-slide-out 0.5s forwards;
  }
  @keyframes mobile-slide-in {
    from { transform: translateY(100%); }
    to { transform: translateX(0); }
  }
  @keyframes mobile-slide-out {
    from { transform: translateY(0); }
    to { transform: translateY(+100%); }
  }
}
@media screen and (max-width: 360px) {
  .header-title {
    display: none;
  }
}
